import React, { useState, useEffect } from "react";
import { Button, Table, Modal, Form, InputGroup, FormControl } from "react-bootstrap";
import { createShipment } from "./api/shiprocket";  // Import your backend API logic
import PaginationComponent from "@/components/PaginationComponent";
import styles from "../styles/shipping.module.css";

const ShippingManagementPage = () => {
  const [shipments, setShipments] = useState([]);
  const [newShipment, setNewShipment] = useState({});
  const [showModal, setShowModal] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage] = useState(5);

  const handleSubmit = async () => {
    // Send the shipment details to backend to create shipment via Shiprocket API
    const response = await createShipment(newShipment);
    
    // Update the shipments table with the newly created shipment details
    if (response.tracking_number) {
      setShipments([...shipments, response]);
      setShowModal(false);
    } else {
      alert("Error creating shipment");
    }
  };

  return (
    <div className="container mt-5">
      <h1 className={`${styles.pageTitle} text-center mb-4`}>Shipping Management</h1>
      <div className="row mb-4">
        <div className="col-md-6">
          <InputGroup>
            <FormControl placeholder="Search by order ID" />
          </InputGroup>
        </div>
        <div className="col-md-6 d-flex justify-content-end">
          <Button variant="primary" onClick={() => setShowModal(true)}>
            Add Shipment
          </Button>
        </div>
      </div>

      <Table striped bordered hover>
        <thead>
          <tr>
            <th>#</th>
            <th>Order ID</th>
            <th>Tracking Number</th>
            <th>Carrier</th>
            <th>Status</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {shipments.map((shipment, index) => (
            <tr key={shipment.id}>
              <td>{index + 1}</td>
              <td>{shipment.order_id}</td>
              <td>{shipment.tracking_number}</td>
              <td>{shipment.carrier}</td>
              <td>{shipment.status}</td>
              <td>
                <Button variant="info" size="sm">View</Button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>

      <Modal show={showModal} onHide={() => setShowModal(false)} centered>
        <Modal.Header closeButton>
          <Modal.Title>Add Shipment</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3">
              <Form.Label>Order ID</Form.Label>
              <Form.Control
                type="text"
                value={newShipment.orderId || ""}
                onChange={(e) => setNewShipment({ ...newShipment, orderId: e.target.value })}
              />
            </Form.Group>
            <Form.Group className="mb-3">
              <Form.Label>Weight</Form.Label>
              <Form.Control
                type="number"
                value={newShipment.weight || ""}
                onChange={(e) => setNewShipment({ ...newShipment, weight: e.target.value })}
              />
            </Form.Group>
            <Form.Group className="mb-3">
              <Form.Label>Destination</Form.Label>
              <Form.Control
                type="text"
                value={newShipment.deliveryCity || ""}
                onChange={(e) => setNewShipment({ ...newShipment, deliveryCity: e.target.value })}
              />
            </Form.Group>
            {/* Additional fields like product details, customer info, etc. */}
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowModal(false)}>
            Close
          </Button>
          <Button variant="primary" onClick={handleSubmit}>
            Create Shipment
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default ShippingManagementPage;
